import React, { Component } from 'react'
import './index.css'
import Color from '../color'

class Home extends Component {

	hexChanged = () => {
		const hex = document.getElementById("hex")
		const rgb = document.getElementById("rgb")
		const hsl = document.getElementById("hsl")
		const main = document.getElementById("main")

		if (!Color.isHex(hex.value)) {
			rgb.value = ""
			hsl.value = ""
			main.style.backgroundColor = "#FFFFFF"
			return
		}
		const rgbObj = Color.hexToRgb(hex.value)

		if (rgbObj) {
			rgb.value = Color.toRgbString(rgbObj.r, rgbObj.g, rgbObj.b)
			const hslObj = Color.rgbToHsl(rgbObj.r, rgbObj.g, rgbObj.b)

			if (hslObj) {
				hsl.value = Color.toHslString(hslObj.h, hslObj.s, hslObj.l)
				main.style.backgroundColor = hsl.value
			}
		}
		else {
			rgb.value = ""
			hsl.value = ""
			main.style.backgroundColor = "#FFFFFF"
		}
	}

	rgbChanged = () => {
		const hex = document.getElementById("hex")
		const rgb = document.getElementById("rgb")
		const hsl = document.getElementById("hsl")
		const main = document.getElementById("main")

		if (!Color.isRgb(rgb.value)) {
			hex.value = ""
			hsl.value = ""
			main.style.backgroundColor = "#FFFFFF"
			return
		}
		const rgbObj = Color.toRgbObject(rgb.value)

		if (rgbObj) {
			hex.value = Color.rgbToHex(rgbObj.r, rgbObj.g, rgbObj.b)
			const hslObj = Color.rgbToHsl(rgbObj.r, rgbObj.g, rgbObj.b)

			if (hslObj) {
				hsl.value = Color.toHslString(hslObj.h, hslObj.s, hslObj.l)
				main.style.backgroundColor = hsl.value
			}
		}
		else {
			hex.value = ""
			hsl.value = ""
			main.style.backgroundColor = "#FFFFFF"
		}
	}

	hexChanged = () => {
		const hex = document.getElementById("hex")
		const rgb = document.getElementById("rgb")
		const hsl = document.getElementById("hsl")
		const main = document.getElementById("main")

		if (!Color.isHex(hex.value)) {
			rgb.value = ""
			hsl.value = ""
			main.style.backgroundColor = "#FFFFFF"
			return
		}
		const rgbObj = Color.hexToRgb(hex.value)

		if (rgbObj) {
			rgb.value = Color.toRgbString(rgbObj.r, rgbObj.g, rgbObj.b)
			const hslObj = Color.rgbToHsl(rgbObj.r, rgbObj.g, rgbObj.b)

			if (hslObj) {
				hsl.value = Color.toHslString(hslObj.h, hslObj.s, hslObj.l)
				main.style.backgroundColor = hsl.value
			}
		}
		else {
			rgb.value = ""
			hsl.value = ""
			main.style.backgroundColor = "#FFFFFF"
		}
	}

	render() {
		return (
			<div className="converter">
				<input type="text" id="hex" name="hex" placeholder="#FF0000" onChange={this.hexChanged} />
				<input type="text" id="rgb" name="rgb" placeholder="rgb(255, 0, 0)" onChange={this.rgbChanged} />
				<input type="text" id="hsl" name="hsl" placeholder="hsl(0, 100%, 50%)" />
			</div>
		)
	}
}
export default Home